

































































import { Component, Prop, Watch, Vue } from 'vue-property-decorator'

import util from '@/utils/tools'
import compConfig from '@/config/componentConfig'
import upload from '@/components/upload.vue'

@Component({
  name: 'ScrollItem',
  components: {
    upload
  }
})
export default class ScrollItem extends Vue {
  @Prop({ default: null })
  private scrolls?: Array<any>

  $evt: any
  private defaultConf: any = util.copyObj(compConfig['scroll-left'])
  private items: Array<any> = this.scrolls || []

  @Watch('scrolls', { deep: true })
  private watchScrolls (val: Array<any>): void {
    this.items = val
  }

  private showClick (banner: any, idx: number): void {
    this.$evt.$emit('click:show', idx, ['outside'])
  }

  private upItem (idx: number): void {
    const tmp: any = util.copyObj(this.items[idx])
    this.items.splice(idx, 1)
    this.items.splice(idx - 1, 0, tmp)
  }

  private downItem (idx: number): void {
    const tmp: any = util.copyObj(this.items[idx])
    this.items.splice(idx, 1)
    this.items.splice(idx + 1, 0, tmp)
  }

  private delItem (idx: number): void {
    this.items.splice(idx, 1)
  }

  private addItem (): void {
    if (this.items.length < 10) {
      this.items.push(util.copyObj(this.defaultConf.action.config[0]))
    } else {
      this.$alert('最多添加10个图片项！')
    }
  }
}
